.form-field {
  width: 100%;
  height: 40px;
  border: none;
  border-radius: 2px;
  white-space: nowrap;
  vertical-align: top;
  background-color: white; }
  .form-field--show-overflow {
    overflow: visible; }
  .form-field__icon {
    padding: 10px;
    height: 100%;
    font-size: 20px;
    vertical-align: top; }
  .form-field__value-box {
    display: inline-block;
    border: none;
    width: calc(100% - 54px);
    height: 100%;
    outline: none;
    text-overflow: ellipsis;
    overflow: hidden; }
    .form-field__value-box:not(input) {
      cursor: pointer;
      padding: 13px 0 0; }
  .form-field__dropdown {
    display: none;
    position: absolute;
    width: 100%;
    transform: translateY(-6px);
    padding: 0;
    background-color: white;
    box-shadow: 0px 6px 20px 0px #eaeaea;
    z-index: 1; }
    .form-field__dropdown.visibile {
      display: block; }
    .form-field__dropdown--occupancy {
      padding: 15px; }
    .form-field__dropdown li {
      padding: 10px;
      text-transform: capitalize; }
      .form-field__dropdown li.active {
        background-color: #eee; }
      .form-field__dropdown li:hover {
        background-color: #5897fb;
        color: white;
        cursor: pointer; }
  .form-field__cta-btn {
    border: none;
    cursor: pointer;
    text-transform: uppercase;
    background-color: #69BE28;
    color: white;
    font-weight: bold; }

.modal-wrapper {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10; }
  .modal-wrapper .modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    min-width: 300px;
    min-height: 200px;
    padding: 15px; }
  .modal-wrapper .modal-header {
    padding: 10px 0; }
    .modal-wrapper .modal-header .title {
      display: inline-block;
      margin: 0; }
    .modal-wrapper .modal-header .close-modal {
      float: right;
      display: inline-block; }
  .modal-wrapper .modal-footer {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 10px 0;
    text-align: center;
    width: 100%; }

.form-container {
  margin: 50px 15px;
  padding: 10px;
  border: none;
  position: relative;
  background-color: #0088CE; }
  .form-container .reservation-form {
    height: 100%;
    padding: 15px 8px 0; }
    .form-container .reservation-form .form-group {
      vertical-align: top;
      position: relative;
      display: inline-block;
      width: calc((100% / 4) - 8px);
      min-width: 200px;
      height: 70px;
      margin-bottom: 15px; }
      .form-container .reservation-form .form-group:not(:first-child) {
        margin-left: 5px; }
      @media (max-width: 61.24em) {
        .form-container .reservation-form .form-group {
          width: calc(50% - 5px); }
          .form-container .reservation-form .form-group:nth-of-type(odd) {
            margin-left: 0; } }
      @media (max-width: 46.24em) {
        .form-container .reservation-form .form-group {
          width: 100%;
          margin-left: 0 !important; } }
      .form-container .reservation-form .form-group .form-label {
        height: 25px;
        color: white;
        font-size: 18px; }

* {
  -webkit-box-sizing: border-box !important;
  box-sizing: border-box !important;
  color: #444;
  padding: 0;
  margin: 0; }

body {
  font-family: 'serif';
  font-size: 14px;
  font-family: 'Roboto', sans-serif; }

ul {
  list-style: none; }

select {
  background-color: white;
  padding: 5px 8px; }

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
